$firstColor: #5465ff;
$secondColor: #788bff;
$thirdColor: #9bb1ff;
$fourthColor: #bfd7ff;
$fifthColor: #e2fdff;

.wp-block-evb-background {
	position: relative;
	overflow: hidden;
	display: flex;
	flex-direction: column;

	* {
		box-sizing: border-box;
	}

	.evbBackground {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		pointer-events: none;
		z-index: 0;
	}

	.backgroundContent {
		width: 100%;
		height: 100%;
		flex: 1;
		max-height: 100%;
		color: #fff;
		display: flex;
		flex-direction: column;
		z-index: 1;

		* {
			z-index: 1;
		}

		p {
			max-width: 100%;
		}

		.block-editor-block-list__layout {
			>* {
				max-width: 100%;
			}
		}
	}
}